import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom'

import PropTypes from 'prop-types';
import clsx from 'clsx';
import { withStyles } from '@mui/styles';
import { createTheme } from '@mui/material/styles';
import TableCell from '@mui/material/TableCell';
import Paper from '@mui/material/Paper';
import { AutoSizer, Column, Table } from 'react-virtualized';
import Button from '@mui/material/Button'
import Box from '@mui/material/Box'

import Confirm from '../components/Confirm'

import { BASE_URL } from '../App'
import { Typography } from '@mui/material';


const styles = (theme) => ({
  flexContainer: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',

  },
  flexContainerList: {
    display: 'flex',
    alignItems: 'center',
    boxSizing: 'border-box',
    overflow: "scroll",
    flexDirection: "column"
  },
  table: {
    // temporary right-to-left patch, waiting for
    // https://github.com/bvaughn/react-virtualized/issues/454
    '& .ReactVirtualized__Table__headerRow': {
      ...(theme.direction === 'rtl' && {
        paddingLeft: '0 !important',
      }),
      ...(theme.direction !== 'rtl' && {
        paddingRight: undefined,
      }),
    },
  },
  tableRow: {
    cursor: 'pointer',
  },
  tableRowHover: {
    '&:hover': {
      backgroundColor: theme.palette.grey[200],
    },
  },
  tableCell: {
    flex: 1,
  },
  noClick: {
    cursor: 'initial',
  },
});


class MuiVirtualizedTable extends React.PureComponent {
  static defaultProps = {
    headerHeight: 48,
    rowHeight: 72,
  };



  getRowClassName = ({ index }) => {
    const { classes, onRowClick } = this.props;

    return clsx(classes.tableRow, classes.flexContainer, {
      [classes.tableRowHover]: index !== -1 && onRowClick == null,
    });
  };

  cellRenderer = ({ cellData, rowData, columnIndex }) => {

    const { columns, classes, rowHeight, onRowClick } = this.props;
    if (columnIndex === 4) {
        return (
          <TableCell
            component="div"
            className={clsx(classes.tableCell, classes.flexContainerList, {
              [classes.noClick]: onRowClick == null,
            })}
            variant="body"
            style={{ height: rowHeight }}
            align={
              (columnIndex != null && columns[columnIndex].numeric) || false
                ? 'right'
                : 'left'
            }
          >
            {cellData.map((provider) => {
                if (provider.id) {
                return (
                  <Link to={`/providers/${provider.id}`}>
                    <Typography key={provider.id}>{provider.last_name}, {provider.first_name}</Typography>
                  </Link>

                )
                }
                else {
                  return (
                    <Typography key="empty">---</Typography>
                  )
                }
            })}
        </TableCell>
        )
    }
    else {
    return (
      <TableCell
        component="div"
        className={clsx(classes.tableCell, classes.flexContainer, {
          [classes.noClick]: onRowClick == null,
        })}
        variant="body"
        style={{ height: rowHeight }}
        align={
          (columnIndex != null && columns[columnIndex].numeric) || false
            ? 'right'
            : 'left'
        }
      >
        {cellData}
      </TableCell>
    );
    }
  };

  headerRenderer = ({ label }) => {
    const { headerHeight, classes } = this.props;

    return (
      <TableCell
        component="div"
        className={clsx(classes.tableCell, classes.flexContainer, classes.noClick)}
        variant="head"
        style={{ height: headerHeight }}
      >
        <span>{label}</span>
      </TableCell>
    );
  };

  render() {
    const { classes, columns, rowHeight, headerHeight, ...tableProps } = this.props;
    return (
      <AutoSizer>
        {({ height, width }) => (
          <Table
            height={height}
            width={width}
            rowHeight={rowHeight}
            gridStyle={{
              direction: 'inherit',
            }}
            headerHeight={headerHeight}
            className={classes.table}
            {...tableProps}
            rowClassName={this.getRowClassName}
          >
            {columns.map(({ dataKey, ...other }, index) => {
              return (
                <Column
                  key={dataKey}
                  headerRenderer={(headerProps) =>
                    this.headerRenderer({
                      ...headerProps,
                      columnIndex: index,
                    })
                  }
                  className={classes.flexContainer}
                  cellRenderer={this.cellRenderer}
                  dataKey={dataKey}
                  {...other}
                />
              );
            })}
          </Table>
        )}
      </AutoSizer>
    );
  }
}

MuiVirtualizedTable.propTypes = {
  classes: PropTypes.object.isRequired,
  columns: PropTypes.arrayOf(
    PropTypes.shape({
      dataKey: PropTypes.string,
      label: PropTypes.string,
      numeric: PropTypes.bool,
      width: PropTypes.number.isRequired,
    }),
  ).isRequired,
  headerHeight: PropTypes.number,
  onRowClick: PropTypes.func,
  rowHeight: PropTypes.number,
};

const defaultTheme = createTheme();
const VirtualizedTable = withStyles(styles, { defaultTheme })(MuiVirtualizedTable);

export default function Patients(props) {
    const [rows, setRows] = useState([])
    const [open, setOpen] = useState({open: false, action: ""})
    const [providers, setProviders] = useState([])

    const { page } = props

      const handleClose = () => {
        setOpen({...open, open: false});
      };


    useEffect(()=> {
        let config = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json',
            },
        }
    
          fetch(BASE_URL+"patients", config)
          .then(res => res.json())
          .then(res => {
                setRows(res.patients)
          })
      }, [])

  return (
    <Box>
    <Paper style={{ height: 600, width: '100%' }}>
      <VirtualizedTable
        setRows= {setRows}
        setOpen= {setOpen}
        rowCount={rows.length}
        rowGetter={({ index }) => rows[index]}
        columns={[
          {
            width: 100,
            label: 'ID',
            dataKey: 'id',
          },
          {
            width: 250,
            label: 'Last Name',
            dataKey: 'last_name',
          },
          {
            width: 250,
            label: 'First Name',
            dataKey: 'first_name',
          },
          {
            width: 200,
            label: 'MRN',
            dataKey: 'mrn',
          },
          {
            dataKey: 'providers',
            label: 'Proivder(s)',
            width: 200,

          },
        ]}
      />
    </Paper>
    </Box>
  );
}